.sb-theme-lime {
  // Prism Theme Colors
  --sbt-theme-color-10: var(--sbt-lime-10);
  --sbt-theme-color-20: var(--sbt-lime-20);
  --sbt-theme-color-30: var(--sbt-lime-30);
  --sbt-theme-color-40: var(--sbt-lime-40);
  --sbt-theme-color-50: var(--sbt-lime-50);
  --sbt-theme-color-60: var(--sbt-lime-60);
  --sbt-theme-color-70: var(--sbt-lime-70);
  --sbt-theme-color-80: var(--sbt-lime-80);
  --sbt-theme-color-90: var(--sbt-lime-90);
  --sbt-theme-color-100: var(--sbt-lime-100);

  // Header colors
  --sbt-color-header-dark: var(--sbt-theme-color-60);
  --sbt-color-header-darker: var(--sbt-theme-color-70);
  --sbt-color-header-light: var(--sbt-theme-color-40);
  --sbt-color-header-lighter: var(--sbt-theme-color-30);
}
